import React from "react";
import { Card, Table } from "react-bootstrap";
import SectionHeading from "../../analytics-new/layout/common/sectionHeading";
import WebsiteSkeleton from "./skeleton/websiteSkeleton";
import { MediaBaseUrl } from "../../../../actions/api";
import { getthemeLayout } from "../../../../utils";

const Website = (props) => {
  const { visitor, loader } = props;

  const Tlist = (e) => {
    return (
      <td>
        <p className="m-0 text-start">{e.param}</p>
      </td>
    );
  };

  return (visitor && Object.keys(visitor).length) || loader ? (
    <div className="col-md-6 mb-5">
      <div className="widget-post-section ">
        <Card className="p-0 card-custom card-stretch">
          <SectionHeading
            title={"Widget"}
            total={0}
            section={2}
            isDashboard={"card-title h5 w-100"}
          />
          <Card.Body className="p-5">
            <Table responsive>
              <thead>
                <tr>
                  <th style={{ width: "50%" }}>Widget Name</th>
                  <th style={{ width: "25%" }}>Visitor</th>
                  <th style={{ width: "25%" }}>Clicks</th>
                </tr>
              </thead>
              <tbody>
                {loader ? (
                  <WebsiteSkeleton />
                ) : (
                  visitor.map((e) => {
                    return (
                      <tr>
                        <td>
                          <div className="">
                            <div class="d-flex align-items-center w-50">
                              <div class="position-relative">
                                <div class="symbol symbol-50 symbol-light me-3 con_user_img cursor-pointer overflow-hidden">
                                  <img
                                    src={`${MediaBaseUrl}icons/svg-icons/${getthemeLayout(e.signageTheme)}.svg`}
                                  />
                                </div>
                              </div>
                              <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
                                <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                                  {e.name}
                                </p>
                              </div>
                            </div>
                          </div>
                        </td>
                        <Tlist param={e.totalVisitors} />
                        <Tlist param={e.totalShowMoreCount} />
                      </tr>
                    );
                  })
                )}
              </tbody>
            </Table>
          </Card.Body>
        </Card>
      </div>
    </div>
  ) : null;
};

export default React.memo(Website);
